
import React, { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import { MessageSquare, X, Send, ShoppingCart, Package, User, Bot, Star, Truck, CreditCard, HeadphonesIcon } from "lucide-react";

const LiveChatSupport = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState([
    {
      id: 1,
      sender: "bot",
      text: "!  ?\n\n \n \n \n \n ",
      timestamp: new Date(),
    },
  ]);
  const [input, setInput] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const [language, setLanguage] = useState("km");
  const messagesEndRef = useRef(null);

  const products = {
    phones: [
      { id: 1, name: "iPhone 15 Pro Max", price: "$1199", rating: 4.8, stock: "In Stock", image: "" },
      { id: 2, name: "iPhone 15", price: "$799", rating: 4.7, stock: "In Stock", image: "" },
      { id: 3, name: "Samsung Galaxy S24 Ultra", price: "$1299", rating: 4.6, stock: "In Stock", image: "" },
      { id: 4, name: "Samsung Galaxy S24", price: "$899", rating: 4.5, stock: "Limited Stock", image: "" },
      { id: 5, name: "Google Pixel 8 Pro", price: "$999", rating: 4.4, stock: "In Stock", image: "" },
    ],
    laptops: [
      { id: 6, name: "MacBook Pro 16-inch M3", price: "$2499", rating: 4.9, stock: "In Stock", image: "" },
      { id: 7, name: "MacBook Air M2", price: "$1199", rating: 4.8, stock: "In Stock", image: "" },
      { id: 8, name: "Dell XPS 13", price: "$1099", rating: 4.6, stock: "In Stock", image: "" },
      { id: 9, name: "HP Spectre x360", price: "$1299", rating: 4.5, stock: "In Stock", image: "" },
      { id: 10, name: "Lenovo ThinkPad X1", price: "$1499", rating: 4.7, stock: "Limited Stock", image: "" },
    ],
    headphones: [
      { id: 11, name: "AirPods Pro 2", price: "$249", rating: 4.7, stock: "In Stock", image: "" },
      { id: 12, name: "Sony WH-1000XM5", price: "$399", rating: 4.8, stock: "In Stock", image: "" },
      { id: 13, name: "Bose QuietComfort", price: "$329", rating: 4.6, stock: "In Stock", image: "" },
    ],
    accessories: [
      { id: 14, name: "Apple Watch Series 9", price: "$399", rating: 4.6, stock: "In Stock", image: "" },
      { id: 15, name: "iPad Pro 12.9", price: "$1099", rating: 4.7, stock: "In Stock", image: "" },
      { id: 16, name: "Magic Keyboard", price: "$299", rating: 4.4, stock: "In Stock", image: "" },
    ],
  };

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSend = () => {
    if (!input.trim()) return;

    const userMsg = {
      id: Date.now(),
      sender: "user",
      text: input,
      timestamp: new Date(),
    };

    setMessages((prev) => [...prev, userMsg]);
    setInput("");
    setIsTyping(true);

    setTimeout(() => {
      const botResponse = getBotResponse(input);
      const botMsg = {
        id: Date.now() + 1,
        sender: "bot",
        text: botResponse.text,
        timestamp: new Date(),
        products: botResponse.products,
        quickReplies: botResponse.quickReplies,
      };

      setMessages((prev) => [...prev, botMsg]);
      setIsTyping(false);
    }, Math.random() * 1000 + 500);
  };

  const handleQuickReply = (reply) => {
    setInput(reply);
    handleSend();
  };

  const toggleLanguage = () => {
    setLanguage((prev) => (prev === "km" ? "en" : "km"));
  };

  const ProductCard = ({ product }) => (
    <div className="border rounded-lg p-4 m-2 flex items-center space-x-4 bg-gray-100">
      <span className="text-2xl">{product.image}</span>
      <div>
        <h3 className="font-bold">{product.name}</h3>
        <p className="text-green-600">{product.price}</p>
        <p className="text-sm">Rating: {product.rating} </p>
        <p className="text-sm">Stock: {product.stock}</p>
      </div>
    </div>
  );

  ProductCard.propTypes = {
    product: PropTypes.shape({
      id: PropTypes.number.isRequired,
      name: PropTypes.string.isRequired,
      price: PropTypes.string.isRequired,
      rating: PropTypes.number.isRequired,
      stock: PropTypes.string.isRequired,
      image: PropTypes.string.isRequired,
    }).isRequired,
  };

  const QuickReplyButton = ({ reply, onClick }) => (
    <button
      className="bg-blue-500 text-white px-3 py-1 rounded-full m-1 hover:bg-blue-600"
      onClick={() => onClick(reply)}
    >
      {reply}
    </button>
  );

  QuickReplyButton.propTypes = {
    reply: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
  };

  const getBotResponse = (text) => {
    const q = text.toLowerCase();

    if (language === "en") {
      if (q.includes("hello") || q.includes("hi") || q.includes("hey")) {
        return {
          text: "Hello! Welcome to our online store. How can I assist you today?\n\n Browse products\n Track orders\n Payment info\n Shipping\n Ask a question",
          quickReplies: ["View all products", "Popular products", "Special offers", "Help me choose"],
        };
      }
      if (q.includes("phone") || q.includes("mobile") || q.includes("smartphone") || q.includes("iphone") || q.includes("samsung")) {
        return {
          text: " Our best phones:\n\nWhich would you like?\n Latest iPhone?\n Samsung Galaxy Series?\n Phones under $500?\n Best camera phone?",
          products: products.phones.slice(0, 3),
          quickReplies: ["iPhone", "Samsung", "Under $500", "Best camera"],
        };
      }
      if (q.includes("laptop") || q.includes("computer") || q.includes("macbook") || q.includes("pc")) {
        return {
          text: " Our best laptops:\n\nFor:\n  Creative work?\n  Business?\n  Gaming?\n  Students?",
          products: products.laptops.slice(0, 3),
          quickReplies: ["MacBook", "Business", "Gaming", "Students"],
        };
      }
      if (q.includes("headphone") || q.includes("earphone") || q.includes("audio") || q.includes("airpods")) {
        return {
          text: " Best headphones for:\n\n  High-quality music\n  Noise cancellation\n  Sports\n  Calls",
          products: products.headphones,
          quickReplies: ["AirPods", "Sony", "Bose", "Under $100"],
        };
      }
      if (q.includes("price") || q.includes("cost") || q.includes("expensive") || q.includes("cheap")) {
        if (q.includes("under") || q.includes("below")) {
          return {
            text: " Affordable products:\n\n Special offers:\n iPhone 15: $799 (was $899)\n Samsung S24: $899 (was $999)\n MacBook Air: $1199 (was $1299)\n\n Sale ends in 3 days!",
            quickReplies: ["Budget products", "Today's offers", "Financing options", "Price guarantee"],
          };
        }
        return {
          text: " Please specify which product's price you'd like to know:\n\n Phones: $199 - $1299\n Laptops: $699 - $2499\n Headphones: $29 - $399\n Smartwatches: $199 - $799\n\nWhich product are you interested in?",
          quickReplies: ["Phones", "Laptops", "Headphones", "Smartwatches"],
        };
      }
      return { text: "Please provide more details so I can assist you!" };
    }

    // Khmer responses
    if (q.includes("hello") || q.includes("hi") || q.includes("hey") || q.includes("") || q.includes("")) {
      return {
        text: "! ! \n\n\n ?\n ?\n ?\n ?",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("phone") || q.includes("mobile") || q.includes("smartphone") || q.includes("") || q.includes("iPhone") || q.includes("samsung")) {
      return {
        text: " \n\n\n iPhone ?\n Samsung Galaxy Series?\n  $500?\n ?",
        products: products.phones.slice(0, 3),
        quickReplies: ["iPhone", "Samsung", " $500", ""],
      };
    }
    if (q.includes("laptop") || q.includes("computer") || q.includes("") || q.includes("macbook") || q.includes("pc")) {
      return {
        text: " \n\n\n  ?\n  ?\n  ?\n  ?",
        products: products.laptops.slice(0, 3),
        quickReplies: ["MacBook", "", "Gaming", ""],
      };
    }
    if (q.includes("headphone") || q.includes("earphone") || q.includes("audio") || q.includes("") || q.includes("airpods")) {
      return {
        text: " \n\n  \n  \n  \n  ",
        products: products.headphones,
        quickReplies: ["AirPods", "Sony", "Bose", " $100"],
      };
    }
    if (q.includes("apple") || (q.includes("iphone") && (q.includes("15") || q.includes("pro") || q.includes("max")))) {
      return {
        text: " iPhone 15 Series - !\n\n \n  48MP\n Chip A17 Pro\n USB-C\n \n\n $799  $1199",
        products: products.phones.filter((p) => p.name.includes("iPhone 15")),
        quickReplies: ["", "", "?", ""],
      };
    }
    if (q.includes("price") || q.includes("cost") || q.includes("") || q.includes("expensive") || q.includes("cheap") || q.includes("")) {
      if (q.includes("under") || q.includes("below") || q.includes("") || q.includes("")) {
        return {
          text: " \n\n \n iPhone 15: $799 ( $899)\n Samsung S24: $899 ( $999)\n MacBook Air: $1199 ( $1299)\n\n  3 !",
          quickReplies: ["", "", "", ""],
        };
      }
      return {
        text: " \n\n : $199 - $1299\n : $699 - $2499\n : $29 - $399\n Smartwatch: $199 - $799\n\n?",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("order") || q.includes("track") || q.includes("status") || q.includes("") || q.includes("") || q.includes("delivery")) {
      return {
        text: " \n\n \n1  'My Account'\n2  'Order History'\n3 \n\n \n\n \n : 1-2 \n : 3-5 \n : !",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("shipping") || q.includes("delivery") || q.includes("") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n \n  $50\n  Premium\n\n \n : 1-2  ($5)\n : 2-3  ($8)\n : 3-5  ()\n\n \n \n \n  ()",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("payment") || q.includes("pay") || q.includes("card") || q.includes("") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n \n ABA Mobile Banking\n ACLEDA Mobile\n Canadia Bank\n Wing Money\n\n \n Visa\n MasterCard\n American Express\n\n \n PayPal\n Cash on Delivery (COD)\n True Money\n\n  100%",
        quickReplies: ["ABA Pay", "", "", "?"],
      };
    }
    if (q.includes("warranty") || q.includes("guarantee") || q.includes("") || q.includes("support") || q.includes("")) {
      return {
        text: "   \n\n \n iPhone/Samsung: 1 \n MacBook/Laptop: 1-2 \n /Accessories: 6  - 1 \n\n \n  24/7\n  ()\n  (14 )\n\n \n Hotline: 023 XXX XXX\n Email: support@shop.com",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("compare") || q.includes("vs") || q.includes("better") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n \n\n iPhone 15 vs Samsung S24:\n iPhone: , iOS\n Samsung: , Android\n\n MacBook vs Dell XPS:\n MacBook: M3 chip, macOS\n Dell: Windows, \n\n?",
        quickReplies: ["iPhone vs Samsung", "MacBook vs PC", "AirPods vs Sony", ""],
      };
    }
    if (q.includes("discount") || q.includes("sale") || q.includes("promotion") || q.includes("offer") || q.includes("") || q.includes("")) {
      return {
        text: "  !\n\n Flash Sale - 48 !\n iPhone 15:  15% ($799)\n MacBook Air:  10% ($1079)\n Samsung S24:  20% ($719)\n\n \n  2  1  ()\n \n  10%\n\n !",
        quickReplies: ["Flash Sale", "", "", ""],
      };
    }
    if (q.includes("spec") || q.includes("technical") || q.includes("feature") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n\n\n iPhone 15 Pro:\n A17 Pro chip\n  48MP\n  6.1 inch\n  3279mAh\n\n MacBook Pro M3:\n M3 chip\n RAM 8-18GB\n SSD 512GB-8TB\n  Retina 16 inch\n\n?",
        quickReplies: ["iPhone specs", "MacBook specs", "Samsung specs", " specs"],
      };
    }
    if (q.includes("stock") || q.includes("available") || q.includes("") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n \n iPhone 15 Series\n MacBook Air/Pro\n Samsung Galaxy\n AirPods Pro\n\n \n iPhone 15 Pro Max ( Titanium)\n MacBook Pro 16-inch\n\n \n AirPods Max ( Space Gray)\n\n   ",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("return") || q.includes("refund") || q.includes("exchange") || q.includes("") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n \n  14 \n \n \n \n\n \n  ()\n  7-10 \n\n \n \n /\n \n\n ",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("review") || q.includes("rating") || q.includes("feedback") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n \n\n iPhone 15 Pro:  (4.8/5)\n\"! !\"\n\n MacBook Air M2:  (4.7/5)\n\"! !\"\n\n AirPods Pro:  (4.6/5)\n\"! !\"\n\n  96% !",
        quickReplies: ["", "", "", ""],
      };
    }
    if (q.includes("account") || q.includes("member") || q.includes("register") || q.includes("login") || q.includes("") || q.includes("")) {
      return {
        text: "   \n\n \n \n \n \n \n\n  Premium:\n  5%\n \n \n \n\n   $10!",
        quickReplies: ["", "", "Premium ", ""],
      };
    }
    if (q.includes("help") || q.includes("support") || q.includes("problem") || q.includes("issue") || q.includes("") || q.includes("")) {
      return {
        text: " \n\n  24/7:\n Hotline: 023-XXX-XXX\n Live Chat: \n Email: support@shop.com\n\n \n \n \n \n \n \n\n ",
        quickReplies: ["", "FAQ", "", ""],
      };
    }
    return { text: " !" };
  };

  return (
    <div className="fixed bottom-4 right-4 z-50">
      {!isOpen ? (
        <button
          className="bg-blue-600 text-white p-4 rounded-full shadow-lg"
          onClick={() => setIsOpen(true)}
          aria-label="Open chatbot"
        >
          <MessageSquare size={24} />
        </button>
      ) : (
        <div className="bg-white w-96 h-[500px] rounded-lg shadow-xl flex flex-col">
          <div className="bg-blue-600 text-white p-4 rounded-t-lg flex justify-between items-center">
            <div className="flex items-center space-x-2">
              <Bot size={24} />
              <span>ShopBot</span>
            </div>
            <div className="flex space-x-2">
              <button
                onClick={toggleLanguage}
                className="text-sm bg-blue-700 px-2 py-1 rounded"
                aria-label="Toggle language"
              >
                {language === "km" ? "EN" : "KM"}
              </button>
              <button
                onClick={() => setIsOpen(false)}
                aria-label="Close chatbot"
              >
                <X size={20} />
              </button>
            </div>
          </div>
          <div className="flex-1 overflow-y-auto p-4 bg-gray-50">
            {messages.map((msg) => (
              <div
                key={msg.id}
                className={`mb-4 flex ${msg.sender === "user" ? "justify-end" : "justify-start"}`}
              >
                <div
                  className={`max-w-xs p-3 rounded-lg ${
                    msg.sender === "user" ? "bg-blue-500 text-white" : "bg-gray-200"
                  }`}
                >
                  {msg.sender === "bot" && (
                    <div className="flex items-center space-x-2 mb-2">
                      <Bot size={16} />
                      <span className="text-sm font-semibold">ShopBot</span>
                    </div>
                  )}
                  <p className="whitespace-pre-wrap">{msg.text}</p>
                  {msg.products && (
                    <div className="mt-2">
                      {msg.products.map((product) => (
                        <ProductCard key={product.id} product={product} />
                      ))}
                    </div>
                  )}
                  {msg.quickReplies && (
                    <div className="mt-2 flex flex-wrap">
                      {msg.quickReplies.map((reply, index) => (
                        <QuickReplyButton
                          key={index}
                          reply={reply}
                          onClick={handleQuickReply}
                        />
                      ))}
                    </div>
                  )}
                  <p className="text-xs mt-1 opacity-70">
                    {msg.timestamp.toLocaleTimeString()}
                  </p>
                </div>
              </div>
            ))}
            {isTyping && (
              <div className="text-gray-500 text-sm">
                ShopBot is typing...
                <span className="animate-pulse">...</span>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>
          <div className="p-4 border-t flex items-center space-x-2">
            <input
              type="text"
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyPress={(e) => e.key === "Enter" && handleSend()}
              placeholder={language === "km" ? "..." : "Type your message..."}
              className="flex-1 p-2 border rounded-lg focus:outline-none"
              aria-label="Chat input"
            />
            <button
              onClick={handleSend}
              className="bg-blue-600 text-white p-2 rounded-lg"
              aria-label="Send message"
            >
              <Send size={20} />
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

LiveChatSupport.propTypes = {
  // No props are passed to LiveChatSupport, so no PropTypes needed here
};

export default LiveChatSupport;
